/*------------------------------------------------------------------
 * This is main responsive css file for theme. It is broken into following sections:
 * 1. Global responsive styles
 * 2. Blog responsive styles
 * 3. Footer responsive styles
 * 4. Header responsive styles
 * 5. Search responsive styles
 * 6. Title area responsive styles
 * 7. Shortcodes responsive styles
 * 8. Custom Post Types responsive styles
-------------------------------------------------------------------*/
/* ==========================================================================
   1. Global responsive styles
   ========================================================================== */
/* common mixins - start */
/* common mixins - end */
/* mixins styles - start */
/* mixins styles - end */
@media only screen and (max-width: 1440px) {
  .error404 .mkdf-page-not-found {
    padding: 0 9%;
  }
}

@media only screen and (max-width: 1024px) {
  body:not(.mkdf-content-is-behind-header):not(.error404) .mkdf-content {
    margin-top: 0 !important;
  }
  body.error404:not(.admin-bar) .mkdf-content {
    height: calc(100vh + 30px);
  }
  .error404 .mkdf-page-not-found {
    padding: 0 12.5%;
    height: 100vh;
  }
  .mkdf-hide-on-tablet {
    display: none !important;
  }
}

@media only screen and (max-width: 768px) {
  .error404 .mkdf-page-not-found {
    padding: 0 30px 20px 80px;
  }
  .error404 .mkdf-page-not-found .mkdf-404-text {
    width: 55%;
  }
}

@media only screen and (max-width: 680px) {
  .error404 .mkdf-page-not-found {
    padding: 42px 30px 20px 42px;
  }
  .error404 .mkdf-page-not-found .mkdf-404-text {
    margin: 25px 0 0;
    width: 72%;
  }
  .mkdf-hide-on-mobile {
    display: none !important;
  }
}

@media only screen and (max-width: 1200px) {
  .mkdf-grid,
  .mkdf-container-inner,
  .mkdf-row-grid-section {
    width: 950px;
  }
}

@media only screen and (max-width: 1024px) {
  .mkdf-grid,
  .mkdf-container-inner,
  .mkdf-row-grid-section {
    width: 768px;
  }
}

@media only screen and (max-width: 768px) {
  .mkdf-grid,
  .mkdf-container-inner,
  .mkdf-row-grid-section {
    width: 600px;
  }
}

@media only screen and (max-width: 680px) {
  .mkdf-grid,
  .mkdf-container-inner,
  .mkdf-row-grid-section {
    width: 420px;
  }
}

@media only screen and (max-width: 480px) {
  .mkdf-grid,
  .mkdf-container-inner,
  .mkdf-row-grid-section {
    width: 300px;
  }
}

@media only screen and (max-width: 320px) {
  .mkdf-grid,
  .mkdf-container-inner,
  .mkdf-row-grid-section {
    width: 90%;
  }
}

@media only screen and (max-width: 1200px) {
