//layout mixins - start

@mixin mkdfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin mkdfTableCellLayout() {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: middle;
}

@mixin mkdfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin mkdfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin mkdfTypographyLayout($important: null) {
    color: inherit $important;
    font-family: inherit $important;
    font-size: inherit $important;
    font-weight: inherit $important;
    font-style: inherit $important;
    line-height: inherit $important;
    letter-spacing: inherit $important;
    text-transform: inherit $important;
}

@mixin mkdfSvgRectLinesAnimation() {
    svg rect {
        @include mkdfTransition(all $default-easing-burger-lines-time ease);
    }
}

@mixin mkdfSvgRectLinesAnimationHover($transition-width: 25px) {
    /*for hover state*/
    svg rect {

        &:nth-of-type(2) {
            transform: translateX(-5px);
            width: $transition-width;
        }

        &:nth-of-type(3) {
            transform: translateX(-10px);
            width: $transition-width;
        }
    }
}

@mixin mkdfSvgArrowAnimation($transition-amount: 8px) {
    svg {
        g {
            @include mkdfTransition(transform $default-easing-svg-arrows-time $default-easing-svg-arrows);
        }
    }

    &:hover {
        svg {
            g:last-child {
                @include mkdfTransform(translateX($transition-amount));
            }
        }
    }
}

@mixin mkdfBlockquote($important : null) {
    @include mkdfRelativeHolderLayout();
    width: auto;
    margin: 10px 0 10px 71px;
    padding: 0 12% 0 15px;
    font-size: 20px;
    line-height: 1.500em;
    text-align: left;
    quotes: none;
    box-sizing: border-box;
    color: #373737;

    &:not(.is-style-large) {
        border-left: 0;
        padding: 0 12% 0 15px;
    }

    &:before {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 1px;
        height: 100%;
        background-color: #000;
    }

    > * {
        @include mkdfTypographyLayout();
        margin: 0;
        display: inline;
    }

    cite {
        font-style: normal $important;
        font-weight: 400 $important;
    }

    cite,
    .wp-block-quote__citation,
    .wp-block-pullquote__citation,
    footer {
        display: block $important;
        margin-top: 10px $important;
        text-align: inherit $important;
        font-size: 14px $important;
        line-height: 1.3em $important;
        letter-spacing: 0 $important;
        font-style: normal  $important;
        font-weight: 400 $important;
        text-transform: none $important;
    }

    @include phone-landscape {
        margin: 10px 0 10px 30px;

        &:not(.is-style-large) {
            padding: 0 11% 0 10px;
        }
    }

}

//layout mixins - end

//transition mixins - start

@mixin mkdfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin mkdfDefaultTransition($transition-param...) {
	$transitions_each: ('-webkit-transition', '-moz-transition', 'transition');
	$string: '';

	@each $var in $transition-param{
		@if $string == '' {
			$string : $var $default-transition-duration $default-easing-function
		} @else {
			$string : $string, $var $default-transition-duration $default-easing-function
		}
	}


	@each $transition in $transitions_each{
	 	#{$transition}: $string;
	}
}

@mixin mkdfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin mkdfTransform($transform-param...) {
    -webkit-transform: $transform-param;    
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin mkdfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin mkdfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

//transition mixins - end

//checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;
%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;
    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }
    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }
    input[type=checkbox] + label span.mkdf-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }
    input[type=checkbox] + label .mkdf-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;
        &:hover {
            cursor: pointer;
        }
    }
    input[type=checkbox] + label .mkdf-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color;
        opacity: 0;
        @include mkdfTransition(opacity $default-transition-duration $default-easing-function);
    }
    input[type=checkbox]:checked + label .mkdf-label-view:after {
        opacity: 1;
    }
}

//checkbox mixins - end

/* common mixins - start */

@mixin mkdfBckImageStyle(){
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true){
    
    @if ($with-hover) {
        
        &:hover {
        
            &:after {
                opacity: 1;
            }
        }
    
        &:after {
            @include mkdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-background-color, .6);
            opacity: 0;
            @include mkdfTransition(opacity $default-transition-duration $default-easing-function);
        }
        
    } @else if ($with-hover: false) {
        &:hover {

            &:after {
                opacity: 0;
            }
        }

        &:after {
            @include mkdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-background-color, .6);
            opacity: 0;
            @include mkdfTransition(opacity $default-transition-duration $default-easing-function);
        }
    } @else {
        @include mkdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-background-color, .6);
        opacity: 0;
        @include mkdfTransition(opacity $default-transition-duration $default-easing-function);
    }
}

@mixin mkdfButtonDefaultStyle() {
    font-family: inherit;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    outline: none;
    font-size: 12px;
    font-weight: 500;
    line-height: inherit;
    letter-spacing: 0.25em;
    text-transform: uppercase;
    box-sizing: border-box;
    margin: 0;
    border-radius: 0;
    @include mkdfDefaultTransition(color, background-color, border-color);
}

@mixin mkdfButtonSize($size: medium) {

    @if ($size == 'small') {
        padding: 7px 40px;

    } @else if ($size == 'medium') {
        padding: 12px 48px;

    } @else if ($size == 'large') {
        padding: 14px 54px;

    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 16px 60px;
    }
}

@mixin mkdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin mkdfButtonSolidColor() {
    color: #000;
    background-color: $first-main-color;
    border: 1px solid transparent;
}

@mixin mkdfButtonSolidHoverColor() {
    color: #000;
    background-color: $secondary-main-color;
}

@mixin mkdfButtonOutlineColor() {
    color: $first-main-color;
    background-color: transparent;
    border: 1px solid $first-main-color;
}

@mixin mkdfButtonOutlineHoverColor() {
    color: #fff;
    background-color: $first-main-color;
    border-color: $first-main-color;
}

@mixin mkdfButtonSmallParams() {
    padding: 7px 40px;
}

@mixin mkdfButtonMediumParams($important: null) {
    padding: 12px 48px $important;
}

@mixin mkdfButtonLargeParams() {
    padding: 14px 54px;
}

@mixin mkdfButtonHugeParams() {
    display: block;
    text-align: center;
    padding: 16px 60px;
}

@mixin mkdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content;
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    width: 100%;
    margin: 0 0 26px;
    padding: 17px 26px;
    font-family: inherit;
    font-size: 15px;
    line-height: 19px;
    font-weight: inherit;
    color: $default-form-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    cursor: text;
    box-sizing: border-box;
    @include mkdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include mkdfPlaceholder {
        color: inherit;
    }
}

/* mixins styles - end */

