//animation mixin - start

@mixin keyframes($name) {
    @-webkit-keyframes #{$name} {
        @content;
    }

    @keyframes #{$name} {
        @content;
    }
}

@mixin animation($name, $duration, $repeat, $timing, $delay) {
    -webkit-animation-name: $name;
    -webkit-animation-duration: $duration;
    -webkit-animation-iteration-count: $repeat;
    -webkit-animation-timing-function: $timing;
    -webkit-animation-delay: $delay;
    -webkit-animation-fill-mode: forwards; /* this prevents the animation from restarting! */

    animation-name: $name;
    animation-duration: $duration;
    animation-iteration-count: $repeat;
    animation-timing-function: $timing;
    animation-delay: $delay;
    animation-fill-mode: forwards; /* this prevents the animation from restarting! */
}

//animation mixin - end

@mixin mkdfImageEasingRight() {
    .mkdf-moving-image-holder {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 100%;

        .mkdf-moving-image {
            @include mkdfTransform(translate3d(0, 0, 0));
            @include mkdfTransition(transform $default-easing-img-out-time $default-easing-img-move);

            img {
                @include mkdfTransform(scale($default-easing-img-scale));
            }
        }
    }
}

@mixin mkdfImageEasingRightHover() {
    &:hover {
        .mkdf-moving-image-holder {
            .mkdf-moving-image {
                @include mkdfTransform(translate3d($default-easing-img-translate, 0, 0));
                @include mkdfTransition(transform $default-easing-img-in-time $default-easing-img-move);

                img {
                }
            }
        }
    }
}

@mixin mkdfLinkUnderline() {
    position: relative;
    width: auto;
    background-repeat: no-repeat;
    background-image: -o-linear-gradient(transparent calc(100% - 1px),currentColor 1px);
    background-image: linear-gradient(transparent calc(100% - 1px),currentColor 1px);
    background-image: -ms-linear-gradient(transparent 95%,currentColor 1px);
    @include mkdfTransition(all .3s ease-out);
    background-size: 0 100%;

    &:hover {
        background-size: 100% 100%;
    }
}

// heading mixins - start

@mixin mkdfDefaultHeadingStyle() {
    @include mkdfHeadingStyle();
    margin: 10px 0;
    color: $default-heading-color;
    font-family: $default-heading-font;
    font-weight: 500;
    text-transform: uppercase;

    -ms-word-wrap: break-word;
    word-wrap: break-word;

    a {
        @include mkdfTypographyLayout();

        &:hover {
            color: $default-heading-color;
        }
    }
}

@mixin mkdfHeadingStyle($with-heading: null, $with-color: true) {

    @if ($with-color) {
        color: $default-heading-color;
    }

    @if ($with-heading == 'h1') {
        @include mkdfH1();
    } @else if ($with-heading == 'h2') {
        @include mkdfH2();
    } @else if ($with-heading == 'h3') {
        @include mkdfH3();
    } @else if ($with-heading == 'h4') {
        @include mkdfH4();
    } @else if ($with-heading == 'h5') {
        @include mkdfH5();
    } @else if ($with-heading == 'h6') {
        @include mkdfH6();
    }
}
@mixin mkdfBody() {
    font-family: $default-text-font;
    font-size: 15px;
    line-height: 1.600em;
    font-weight: 400;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

@mixin mkdfH1() {
    font-size: 54px;
    line-height: 1.074em;
    letter-spacing: 0.07em;
}

@mixin mkdfH2() {
    font-size: 42px;
    line-height: 1.190em;
    letter-spacing: 0.06em;
}

@mixin mkdfH3() {
    font-size: 30px;
    line-height: 1.133em;
    letter-spacing: 0.06em;
}

@mixin mkdfH4() {
    font-size: 20px;
    line-height: 1.200em;
    letter-spacing: 0.06em;
}

@mixin mkdfH5() {
    font-size: 16px;
    line-height: 1.250em;
    letter-spacing: 0.06em;
}

@mixin mkdfH6() {
    font-size: 13px;
    line-height: 1.385em;
    letter-spacing: 0.06em;
}

// heading mixins - end